import React from 'react'
import md5 from 'md5'
import ProbeReport from '../components/Probe/Report'

import fetchUtil from 'utils/fetchUtil'
import { getDomain } from 'utils/domain'

import DATA from '../components/Probe/data'
import BARDATA from '../components/Probe/barData'

const HOST = getDomain()

const getTimeStr = (date) => {
  const timeStr = date.getFullYear() + '/' + (date.getMonth() + 1) + '/' + date.getDate()
  return timeStr
}

const getSumOfTimeArray = (arr = [], start, end) => {
  return arr.slice(start, end).reduce((prev, cur) => prev + cur.num, 0)
}

export const Promised = (Wrapped) => class extends React.Component {
  state = {
    perHourData: {
      amNum: 0,
      pmNum: 0,
      nightNum: 0,
      timeArray: []
    },
    perDayData: []
  }

  async componentWillMount () {
    const date = new Date()
    console.log(getTimeStr(date))
    const startTime = (new Date(getTimeStr(date)).getTime()) / 1000
    const endTime = ((date.getTime()) / 1000).toFixed()
    const startDay = endTime - (6 * 24 * 3600)
    const signParam = `app_key=d93823b9e5d089a338a6c0b860e61a7b` +
      `&app_secret=bfe96bf06c1e9ea185202da3f413f126&method=GET&ts=${endTime}`
    const sign = md5(signParam)
    const signParams = `app_key=d93823b9e5d089a338a6c0b860e61a7b&method=GET&ts=${endTime}&sign=${sign}`

    const hourParams = `storeId=10021141&startTime=${startTime}&endTime=${endTime}&${signParams}`
    const dayParams = `storeId=10021141&startTime=${startDay}&endTime=${endTime}&${signParams}`
    const timeApiUrl = `${HOST}/bo/store/v1/storePerSummary/hour?${hourParams}`
    const dayApiUrl = `${HOST}/bo/store/v1/storePerSummary/day?${dayParams}`

    fetchUtil.getJSON(timeApiUrl).then(data => {
      this.setState({ perHourData: data })
    })['catch'](e => {
      this.setState({ perHourData: DATA })
      console.warn(e, '拉取数据错误')
    })

    fetchUtil.getJSON(dayApiUrl).then(data => {
      this.setState({ perDayData: data.timeArray })
    })['catch'](e => {
      this.setState({ perDayData: BARDATA.data.timeArray })
      console.warn(e, '拉取数据错误')
    })
  }

  getChartData = () => {
    const { timeArray } = this.state.perHourData

    return [{
      time: '0',
      num: getSumOfTimeArray(timeArray, 0, 8)
    }].concat(timeArray.slice(8, 24))
  }

  render () {
    const { perDayData, perHourData } = this.state
    const { allNum, amNum, pmNum, nightNum } = perHourData
    const chartData = this.getChartData()
    const data = {
      allNum,
      amNum,
      pmNum,
      nightNum,
      chartData,
      perDayData
    }

    return <Wrapped {...data} />
  }
}

export default Promised(ProbeReport)



